import { AppState, AppReducer, initialState } from 'store/constants';
import { ROUTES } from 'store/constants/router';
import { prepareCatalogItemsMap } from 'store/reducers/view/catalogPage';
import { selectPathname } from 'store/selectors';

const getScreenIndexMap = ({ catalogPage: { listIndexOffset, order } }: AppState): Record<string, number> =>
    new Array(3).fill(0).reduce(
        (result, _, index) => ({
            ...result,
            [order[index + listIndexOffset]]: index + 1,
        }),
        {} as Record<string, number>,
    );

export const catalogPagePortalView: AppReducer = (state, action) => {
    const { catalogPage } = state;

    switch (action.type) {
        case 'SHOW_ITEMS': {
            catalogPage.order = Object.keys(action.payload.stock);

            const [firstItemId] = catalogPage.order;
            catalogPage.selectedItemId = firstItemId;
            catalogPage.listIndexOffset = initialState.catalogPage.listIndexOffset;

            break;
        }
        case 'ADD_ITEM': {
            if (selectPathname(state) === ROUTES.CATALOG) {
                if (catalogPage.order.includes(action.payload.item)) {
                    catalogPage.selectedItemId = action.payload.item;

                    if (!action.payload.skipFocus) {
                        catalogPage.listIndexOffset = catalogPage.order.findIndex(
                            (itemId) => itemId === action.payload.item,
                        );
                    }
                }
            }

            break;
        }

        case 'REMOVE_ITEM': {
            if (selectPathname(state) === ROUTES.CATALOG) {
                catalogPage.selectedItemId = action.payload.item;
                catalogPage.listIndexOffset = catalogPage.order.findIndex((itemId) => itemId === action.payload.item);
            }

            break;
        }

        case 'REMOVE_ALL': {
            catalogPage.selectedItemId = initialState.catalogPage.selectedItemId;
            catalogPage.listIndexOffset = initialState.catalogPage.listIndexOffset;

            break;
        }

        case 'catalogPortal/LIST_SCROLL': {
            catalogPage.listIndexOffset = action.payload;
            catalogPage.selectedItemId = catalogPage.order[action.payload];

            break;
        }

        default:
            break;
    }

    catalogPage.screenIndexMap = getScreenIndexMap(state);
    catalogPage.itemsMap = prepareCatalogItemsMap(state);
};
